<template>
  <div class="container text-center">
    <div class="d-flex justify-content-center mt-sm-5">
      <!-- <div class="bg-white m-5 mx-auto p-4 w-auto shadow rounded login-frm"> -->
      <div class="bg-white login-frm p-3 shadow">
        <div class="fadeIn first">
          <img class="mb-2" style="width: 100px" src="../assets/logo.svg" />
          <h6 class="mb-4 fs-3 fw-bold text-center">ورود به حساب</h6>
        </div>
        <div
          v-if="error"
          class="bg-danger fw-bold mb-4 px-4 py-2 rounded text-white"
        >
          {{ error }}
        </div>

        <form>
          <div class="mb-3 text-start">
            <label for="login" class="form-label">تلفن همراه</label>
            <input
              type="email"
              class="form-control rounded-0"
              id="login"
              placeholder="0912*******"
              tabindex="1"
              v-model="email"
              v-on:keyup="focusOnPassword"
            />
          </div>

          <div class="mb-3 text-start">
            <label for="password" class="form-label">رمز عبور</label>

            <div class="input-group" lang="en" dir="rtl">
              <span class="input-group-btn">
                <button
                  class="btn btn-secondary rounded-0"
                  type="button"
                  tabindex="3"
                  @click="togglePass"
                >
                  <span v-show="showEyeSlash === true">
                    <i class="fas fa-eye-slash"></i>
                  </span>
                  <span v-show="showEyeSlash === false">
                    <i class="fas fa-eye"></i>
                  </span>
                </button>
              </span>
              <input
                type="password"
                id="password"
                class="form-control"
                placeholder="*******"
                dir="ltr"
                tabindex="2"
                v-model="password"
                @keypress="inputAuth"
              />
            </div>
          </div>

          <div class="mb-3 mt-5 text-end">
            <button
              type="button"
              class="btn btn-theme w-100 fw-bold rounded-0"
              tabindex="4"
              @click="auth"
            >
              {{ login_text }}
            </button>
          </div>
        </form>

        <div id="formFooter" class="mb-3 mt-5">
          <router-link
            :to="{ name: 'RegisterLogin' }"
            class="btn btn-primary btn-sm w-100"
          >
            ورود با یکبار رمز
          </router-link>

          <router-link
            :to="{ name: 'Register' }"
            class="btn btn-primary btn-sm w-100 mt-2"
          >
            حساب کاربری ندارید؟ ثبت نام کنید
          </router-link>

          <router-link
            :to="{ name: 'Forgetpassword' }"
            class="btn btn-primary btn-sm w-100 my-2"
          >
            رمز عبور خود را فراموش کرده اید؟
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ArefApi from "../libs/api.js";
import utils from "../libs/utils";

export default {
  name: "login",
  created() {
    this.utils = new utils();
  },
  data() {
    return {
      showEyeSlash: true,
      email: null,
      password: null,
      error: null,
      login_text: "ورود",
      pathAfterLogin: this.$route.params.pathAfterLogin
        ? this.$route.params.pathAfterLogin
        : null,
    };
  },
  methods: {
    togglePass: function () {
      this.showEyeSlash = !this.showEyeSlash;
      document.querySelector("#password").type = this.showEyeSlash
        ? "password"
        : "text";
    },
    focusOnPassword: function (e) {
      this.email = this.utils.perToEn(this.email);
      if (e.keyCode === 13) {
        document.querySelector("#password").focus();
      }
    },
    inputAuth: function (e) {
      if (e.keyCode === 13) {
        this.auth();
      }
    },
    auth: function () {
      this.error = null;
      if (!this.validateLoginFrm()) {
        return;
      }
      this.login_text = "لطفا کمی صبر کنید ...";
      let loginData = {
        email: this.email,
        password: this.password,
      };
      const arefApi = new ArefApi(this);
      arefApi
        .login(loginData)
        .then((response) => {
          this.utils.afterSuccessLogin(response);
          let routeName = "Shop";

          switch (response.data.group.type) {
            case "admin":
              routeName = "AdminHome";
              break;

            default:
              arefApi
                .getCart()
                .then((resp) => {
                  this.$store.commit("set", [
                    "cartCount",
                    this.utils.getCartCount(resp.data),
                  ]);
                  this.$store.commit("set", ["cartAmount", resp.data.amount]);
                })
                .catch((err) => {
                  this.utils.showErrors(this, err);
                });
              break;
          }
          if (
            !this.pathAfterLogin ||
            this.$store.state.pathAfterLogin == "/login"
          ) {
            this.$router.push({ name: routeName });
            this.$store.commit("set", ["pathAfterLogin", null]);
          } else {
            const tmpPath =
              this.pathAfterLogin || this.$store.state.pathAfterLogin;
            this.$store.commit("set", ["pathAfterLogin", null]);
            this.$router.push({ path: `/${tmpPath}` });
          }
        })
        .catch(() => {
          this.error = "نام کاربری یا رمز عبور اشتباه است";
          this.login_text = "ورود";
        });
    },
    validateLoginFrm: function () {
      if (!this.email) {
        this.error = "لطفا نام کاربری را وارد فرمایید";
        return false;
      }
      if (this.email.length < 4) {
        this.error = "لطفا نام کاربری را صحیح وارد فرمایید";
        return false;
      }
      if (!this.password) {
        this.error = "لطفا رمز عبور را وارد فرمایید";
        return false;
      }
      return true;
    },
    checkIsLogged: function () {
      if (this.$store.state.token) {
        if (
          !this.pathAfterLogin ||
          this.$store.state.pathAfterLogin == "/login"
        ) {
          this.$router.push({ name: "Home" });
          this.$store.commit("set", ["pathAfterLogin", null]);
        } else {
          const tmpPath =
            this.pathAfterLogin || this.$store.state.pathAfterLogin;
          this.$store.commit("set", ["pathAfterLogin", null]);
          this.$router.push({ path: `/${tmpPath}` });
        }
      }
    },
  },
  mounted() {
    this.checkIsLogged();
    document.querySelector("#login").focus();
  },
};
</script>
<style scoped>
.login-frm {
  min-width: 300px;
}
</style>